import { useEffect } from 'react';

export const useOutsideClick = (ref: any, callback: any) => {
  useEffect(() => {
    const handleClick = () => {
      callback();
    };

    document.addEventListener('click', handleClick);

    return () => {
      document.removeEventListener('click', handleClick);
    };
  }, [callback, ref]);

  return ref;
};
